body {
  transition: all 0.3s ease-in;
}

section {
  min-height: 80px;
  padding: 40px 10px;
  border-bottom: 1px solid #ddd;
}

.heading {
  display: flex;
}
.color-wr {
  display: flex;
}
.box {
  width: 40px;
  height: 50px;
  margin: 6px;
}

.box-1 {
  background-color: $abbey;
}
.box-2 {
  background-color: $raven;
}
.box-3 {
  background-color: $oslo;
}
.box-4 {
  background-color: $mischka;
}
.box-5 {
  background-color: $ghost;
}
.box-6 {
  background-color: $iron;
}
.box-7 {
  background-color: $porcelain;
}

.box-summer-lighter {
  background-color: $summer-lighter;
}
.box-summer-light {
  background-color: $summer-light;
}
.box-summer {
  background-color: $summer;
}
.box-summer-dark {
  background-color: $summer-dark;
}
.box-summer-darker {
  background-color: $summer-darker;
}

.box-shamrock-lighter {
  background-color: $shamrock-lighter;
}
.box-shamrock-light {
  background-color: $shamrock-light;
}
.box-shamrock {
  background-color: $shamrock;
}
.box-shamrock-dark {
  background-color: $shamrock-dark;
}
.box-shamrock-darker {
  background-color: $shamrock-darker;
}

.box-tulip-lighter {
  background-color: $tulip-lighter;
}
.box-tulip-light {
  background-color: $tulip-light;
}
.box-tulip {
  background-color: $tulip;
}
.box-tulip-dark {
  background-color: $tulip-dark;
}
.box-tulip-darker {
  background-color: $tulip-darker;
}

.box-flame-lighter {
  background-color: $flame-lighter;
}
.box-flame-light {
  background-color: $flame-light;
}
.box-flame {
  background-color: $flame;
}
.box-flame-dark {
  background-color: $flame-dark;
}
.box-flame-darker {
  background-color: $flame-darker;
}

