.btn {
  margin: 6px;
  &-full-width {
    margin-inline: 0;
  }
}

.headings {
  display: flex;
  div {
    padding: 2rem;
  }
}

.texts {
  padding-inline: 2rem;
}

.alert {
  margin-bottom: 6px;
}
